  table {
    text-align: center;
  }
  
  .table-container {
    width: 100%;
    height: auto;
    overflow: scroll;
  }
  
  table th,
  table td {
    white-space: nowrap;
    padding: 10px 20px;
    font-family: Arial;
  }
  
  table tr th:first-child,
  table td:first-child {
    position: sticky;
    width: 100px;
    left: 0;
    z-index: 10;
    background: #fff;
  }
  
  table tr th:first-child {
    z-index: 11;
  }
  
  table tr th {
    position: sticky;
    top: 0;
    z-index: 9;
    background: #fff;
  }